document.getElementById("add").onclick = function inputCheck(e) {
  e.preventDefault();
  let enteredValue = document.getElementById("entering").value.trim();
  if (enteredValue.length > 0) {
    addItem(enteredValue);
    clearValue(e);
  };
};

function addItem(enteredValue) {
  let ol = document.getElementById("list");
  let span = document.createElement("span");
  span.contentEditable = 'false';
  const li = document.createElement("li");
  span.innerHTML = enteredValue;
  li.onclick = function markAsCompleted() { 
    if (span.contentEditable == 'false'){  
    this.classList.toggle("completed");
    filterTasksByCompletion();
    };
  };
  addButtonDeletion(li);
  addButtonPerformed(li, span);
  li.append(span);
  ol.prepend(li);
}

function addButtonDeletion(li) {
  const x = document.createElement("div");
  x.classList.add("delete");
  x.onclick = function deleteItem() {
    event.stopPropagation();
    this.parentNode.remove();
  };
  li.append(x);
}

function addButtonPerformed(li,span) {
    const img = document.createElement("img");
    img.src = "./pencil.png";
    img.classList.add("buttonPerformed");
    img.onclick = function markAsDone() {
     event.stopPropagation();
        if (!(li.classList.contains("completed"))){
            span.contentEditable = 'true';
            CarriageMovement(span);
        };
    };
    span.onblur = function terminationOfInput(){
        span.contentEditable = 'false';
    };
    li.append(img);
}


function CarriageMovement(span) {
    const range = document.createRange();
    range.selectNodeContents(span);
    range.collapse(false);
    const sel = window.getSelection();
    sel.removeAllRanges();
    sel.addRange(range);
}

function clearValue(e) {
  e.preventDefault();
  document.getElementById("entering").value = "";
};

document.getElementById("clear").onclick = clearValue;

function filterTasksByCompletion() {
  let select = document.querySelector("select");
  let li = document.querySelectorAll("li");
  switch (select.value) {
    case "all":
      for (let i = 0; i < li.length; i++) {
        li[i].classList.remove("hide");
      }
      break;
    case "unfinished":
      for (let i = 0; i < li.length; i++) {
        li[i].classList.remove("hide");
      }
      for (let i = 0; i < li.length; i++) {
        if (li[i].classList.contains("completed")) {
          li[i].classList.add("hide");
        }
      }
      break;
    case "completed":
      for (let i = 0; i < li.length; i++) {
        li[i].classList.remove("hide");
      }
      for (let i = 0; i < li.length; i++) {
        if (!li[i].classList.contains("completed")) {
          li[i].classList.add("hide");
        }
      }
      break;
  };
}

document.getElementById("filter").onchange = filterTasksByCompletion;
